// Personal Finance Dashboard - Israeli Edition
// Main application entry point - Fixed for Vite production builds

console.log(' Israeli Finance Dashboard - Starting up...');

// Static ES6 imports that work with Vite bundling
import { IsraeliSalaryCalculator } from './utils/israeli-salary-calculator.js';
import { FINANCIAL_TERMS } from './utils/hebrew-financial-terms.js';
import { ISRAELI_FINANCIAL_TIPS } from './utils/israeli-financial-tips.js';
import { ThemeToggle } from './components/ThemeToggle.js';
import { TransactionForm } from './components/TransactionForm.js';
import { CategoryManager } from './components/CategoryManager.js';
import { DataAPI, DataManager } from './data/index.js';

// Make components available globally for backward compatibility
window.IsraeliSalaryCalculator = IsraeliSalaryCalculator;
window.HEBREW_FINANCIAL_TERMS = FINANCIAL_TERMS;
window.ISRAELI_FINANCIAL_TIPS = ISRAELI_FINANCIAL_TIPS;
window.ThemeToggle = ThemeToggle;
window.TransactionForm = TransactionForm;
window.CategoryManager = CategoryManager;
window.DataAPI = DataAPI;
window.DataManager = DataManager;

// Initialize app when DOM is loaded
document.addEventListener('DOMContentLoaded', () => {
    initializeApp();
});

async function initializeApp() {
    console.log(' ES6 modules loaded via static imports');
    
    // Load remaining components via script tags (those with conditional exports)
    await loadScriptBasedComponents();
    
    // Hide loading screen and show app
    setTimeout(() => {
        const loading = document.getElementById('loading');
        const app = document.getElementById('app');
        
        if (loading) loading.style.display = 'none';
        if (app) app.style.display = 'block';
        
        console.log(' App initialized successfully');
    }, 1000);
    
    // Set up event listeners
    setupEventListeners();
    
    // Initialize Hebrew number formatting
    initializeHebrewFormatting();
    
    // Check for saved data
    checkStoredData();
    
    // Initialize UI components
    initializeUIComponents();
    
    // Initialize theme system
    initializeThemeSystem();
}

async function loadScriptBasedComponents() {
    // Only load components that have conditional exports and can't be imported as ES6 modules
    const componentFiles = [
        './src/utils/israeli-formatting.js',
        './src/components/Button.js',
        './src/components/Input.js', 
        './src/components/Navigation.js',
        './src/components/Modal.js',
        './src/components/Toast.js',
        './src/components/Dashboard.js',
        './src/components/TransactionList.js',
        './src/components/Charts.js'
    ];

    try {
        // Load remaining component files using script tags
        for (const file of componentFiles) {
            await loadScript(file);
        }
        
        console.log(' Hebrew UI Components loaded successfully');
        console.log(' Data Management System loaded successfully');
        
        // Check if critical components are available
        const criticalComponents = ['Button', 'Toast', 'Modal'];
        const missingComponents = criticalComponents.filter(comp => !window[comp]);
        
        if (missingComponents.length > 0) {
            console.warn(' Some critical components are missing:', missingComponents);
        }
        
    } catch (error) {
        console.error(' Error loading components:', error);
        
        // Try to show error via basic alert if HebrewToasts is not available
        if (typeof HebrewToasts !== 'undefined') {
            HebrewToasts.error('    ');
        } else {
            console.error('Toast system not available - using basic alert');
            alert('     -      ');
        }
    }
}

function loadScript(src) {
    return new Promise((resolve, reject) => {
        const script = document.createElement('script');
        script.src = src;
        script.onload = () => {
            console.log(` Script loaded: ${src}`);
            resolve();
        };
        script.onerror = (error) => {
            console.error(` Failed to load script: ${src}`, error);
            
            // Show user-friendly error for critical components
            if (src.includes('Button') || src.includes('Toast') || src.includes('Modal')) {
                console.warn(` Critical UI component ${src} failed to load - some UI features may be unavailable`);
            }
            
            // Don't reject to allow other scripts to continue loading
            resolve(); // Resolve instead of reject to prevent stopping the entire loading process
        };
        document.head.appendChild(script);
    });
}

function setupEventListeners() {
    // Start app button
    window.startApp = function() {
        console.log(' Starting main application...');
        
        // Show demo of our component library
        if (window.ComponentUtils) {
            showComponentDemo();
        } else {
            // Fallback for when components aren't loaded yet
            HebrewToasts?.info('   ...') || 
            alert(' !    .');
        }
    };
    
    // Navigation clicks
    document.querySelectorAll('.nav a').forEach(link => {
        link.addEventListener('click', (e) => {
            e.preventDefault();
            const section = e.target.getAttribute('href').substring(1);
            console.log(`Navigating to: ${section}`);
            
            // Show appropriate section
            if (window.HebrewToasts) {
                HebrewToasts.info(` : ${getSectionName(section)}`);
            }
        });
    });
}

function getSectionName(section) {
    const sectionNames = {
        'dashboard': ' ',
        'transactions': '', 
        'budget': '',
        'reports': '',
        'settings': ''
    };
    return sectionNames[section] || section;
}

function showComponentDemo() {
    // Create and show component demo
    const demo = ComponentUtils.createDemo();
    
    // Replace main content with demo
    const mainContent = document.querySelector('.main');
    if (mainContent) {
        // Store original content
        const originalContent = mainContent.innerHTML;
        
        // Add back button
        const backButton = new Button({ type: 'secondary', icon: '' })
            .render('  ', () => {
                mainContent.innerHTML = originalContent;
                // Re-attach event listeners
                setupEventListeners();
                HebrewToasts.info('  ');
            });
        
        demo.insertBefore(backButton, demo.firstChild);
        
        // Replace content
        mainContent.innerHTML = '';
        mainContent.appendChild(demo);
        
        // Show success message
        HebrewToasts.success('    !', ' ');
    }
}

function initializeUIComponents() {
    // Initialize global UI components after everything is loaded
    if (typeof HebrewNavigation !== 'undefined') {
        console.log(' UI Components ready');
        
        // Add component demo button to header if in development
        if (window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1') {
            addDemoButton();
        }
        
        // Initialize tooltips, keyboard shortcuts, etc.
        initializeKeyboardShortcuts();
        
        // Show welcome toast
        setTimeout(() => {
            if (window.HebrewToasts) {
                HebrewToasts.info('    !', '');
            }
        }, 2000);
    }
}

function addDemoButton() {
    // Add demo button to header for development
    const headerContent = document.querySelector('.header-content');
    if (headerContent && window.Button) {
        const demoButton = new Button({ 
            type: 'secondary', 
            size: 'sm',
            style: { 
                marginRight: '1rem',
                backgroundColor: 'rgba(255, 255, 255, 0.2)',
                color: 'white',
                border: '1px solid rgba(255, 255, 255, 0.3)'
            }
        }).render(' Demo', () => {
            if (window.ComponentUtils) {
                showComponentDemo();
            }
        });
        
        headerContent.appendChild(demoButton);
    }
}

function initializeKeyboardShortcuts() {
    // Add keyboard shortcuts for better UX
    document.addEventListener('keydown', (e) => {
        // Ctrl/Cmd + D for demo
        if ((e.ctrlKey || e.metaKey) && e.key === 'd') {
            e.preventDefault();
            if (window.ComponentUtils) {
                showComponentDemo();
            }
        }
        
        // Escape to close modals
        if (e.key === 'Escape') {
            // Close any open modals
            document.querySelectorAll('.modal.show').forEach(modal => {
                if (modal.close) modal.close();
            });
        }
    });
}

function initializeHebrewFormatting() {
    // Initialize Hebrew number formatting and Israeli-specific utilities
    if (typeof IsraeliFormatter !== 'undefined') {
        console.log(' Enhanced Hebrew formatting initialized');
        
        // Create global formatter instance
        window.israeliFormatter = new IsraeliFormatter();
        console.log(' Israeli Formatter loaded successfully');
        
        // Test formatting functions
        console.log('Currency test (basic):', window.israeliFormatter.formatCurrency(1234.56));
        console.log('Currency test (compact):', window.israeliFormatter.formatCurrency(1500000, { compact: true }));
        console.log('Currency test (no symbol):', window.israeliFormatter.formatCurrency(999.99, { showSymbol: false }));
        
        // Test number formatting
        console.log('Number test (basic):', window.israeliFormatter.formatNumber(1234567));
        console.log('Number test (compact):', window.israeliFormatter.formatNumber(2500000, { compact: true }));
        
        // Test date formatting
        console.log('Date test (medium):', window.israeliFormatter.formatDate(new Date(), 'medium'));
        console.log('Date test (long):', window.israeliFormatter.formatDate(new Date(), 'long'));
        
        // Test Israeli-specific formatting
        console.log('Tax year test:', window.israeliFormatter.getTaxYear());
        console.log('Phone test:', window.israeliFormatter.formatPhone('0523456789'));
        console.log('ID test:', window.israeliFormatter.formatIsraeliID('123456789'));
        console.log('Percentage test:', window.israeliFormatter.formatPercentage(0.155));
        
        // Display current tax year info
        const currentTaxYear = window.israeliFormatter.getCurrentTaxYearInfo();
        console.log(' Current tax year:', currentTaxYear.displayName);
        console.log(' Tax year period:', currentTaxYear.period);
    }
}

function checkStoredData() {
    // Check if we have any stored financial data
    setTimeout(() => {
        if (window.DataAPI) {
            console.log(' DataAPI loaded successfully - checking stored data...');
            // Add data checking logic here when DataAPI is ready
        } else {
            console.log(' DataAPI not loaded yet - will check data later');
        }
    }, 1000);
}

function initializeThemeSystem() {
    // Initialize theme system with Hebrew support
    if (typeof ThemeToggle !== 'undefined') {
        console.log(' Initializing theme system...');
        
        // Create theme toggle instance
        const themeToggle = new ThemeToggle({
            showLabel: true,
            position: 'top-right'
        });
        
        // Add to navigation or header
        const headerActions = document.querySelector('.header-content');
        if (headerActions) {
            const toggleContainer = document.createElement('div');
            toggleContainer.className = 'theme-toggle-container';
            toggleContainer.style.marginLeft = 'var(--spacing-md)';
            
            // Create toggle button HTML
            toggleContainer.innerHTML = themeToggle.getHTML();
            headerActions.appendChild(toggleContainer);
            
            // Set up click handler
            const toggleButton = toggleContainer.querySelector('.theme-toggle');
            if (toggleButton) {
                toggleButton.addEventListener('click', () => {
                    themeToggle.toggleTheme();
                });
            }
        } else {
            // Fallback: add as fixed position toggle
            document.body.appendChild(themeToggle.render());
            themeToggle.toggleButton.classList.add('theme-toggle-fixed', 'theme-toggle-top-right');
        }
        
        // Listen for theme changes
        document.addEventListener('themeChanged', (e) => {
            console.log(' Theme changed to:', e.detail.theme);
            
            // Show theme change notification
            if (window.HebrewToasts) {
                const message = e.detail.theme === 'dark' ? '  ' : '  ';
                HebrewToasts.info(message, ' ');
            }
        });
        
        // Apply saved theme on startup
        const savedTheme = localStorage.getItem('theme') || 'light';
        document.documentElement.setAttribute('data-theme', savedTheme);
        
        console.log(' Theme system initialized');
    } else {
        console.warn(' ThemeToggle component not loaded');
    }
}

// Error handling
window.addEventListener('error', (e) => {
    console.error('Application error:', e.error);
    // Error reporting will be implemented in future phases
});

// Service worker registration (for PWA functionality)
if ('serviceWorker' in navigator) {
    window.addEventListener('load', () => {
        // Use correct path for service worker - works for both local and GitHub Pages
        navigator.serviceWorker.register('./sw.js')
            .then(registration => {
                console.log('SW registered: ', registration);
            })
            .catch(registrationError => {
                console.log('SW registration failed: ', registrationError);
            });
    });
}

console.log(' Main.js loaded successfully');